import React,{useState} from 'react'

function Sen2(props) {
    
    const [answer, setAnswer] = useState("")

    const handleChange = (e) => { setAnswer(e.target.value) }

    return (
        <div>
            <p>[הקשר: רבקה נשאלה אם היתה בטיול מהעבודה]<br/>
            רבקה: <b>"אני הלכתי - ולו בשביל החוויה"</b></p>

            <label>
                <input
                    type="radio" name="answer" value="ok" checked={answer==="ok"}        
                    onChange={handleChange}
                />
                בסדר
            </label>
            <br />
            <label>
                <input
                    type="radio" name="answer" value="weird" checked={answer==="weird"}        
                    onChange={handleChange}
                />
                מוזר
            </label>
            <br/>
            <br/>
            <button onClick={() => props.func(2,answer)}>הבא</button>


        </div>
    )
}

export default Sen2