import './App.css';
import Sen1 from './Sen1'
import Sen2 from './Sen2'
import {useState, useEffect} from 'react'
import emailjs from 'emailjs-com'

function App() {

  const [results, setresults] = useState(["",""])
  const [senNo, setSenNo] = useState(1)

  const addRes = (no, res) => {
    const newRes = [...results]
    newRes[no-1] = res
    setresults(newRes)
    setSenNo(prev => prev+1)
  }

  useEffect(() => {
    if (senNo===3) {
      // alert(`a1 is ${results[0]} and a2 is ${results[1]}`)
      emailjs.send('service_v3wl92m', 'template_reyg87b',
        {a1: results[0], a2: results[1] },'user_g3B6KiFwLhOawsUEW5ab8')
    }
  }, [senNo])

  return (
    <div className="App">
      {senNo<3 && <p>האם המשפט הבא נשמע בסדר או מוזר?</p>}
      {senNo===1 && <Sen1 func={addRes} />}
      {senNo===2 && <Sen2 func={addRes} />}
      {senNo>2 && <p>תודה על השתתפותך</p>}
    </div>
  );
}

export default App;
