import React,{useState} from 'react'

function Sen1(props) {
    
    const [answer, setAnswer] = useState("")

    const handleChange = (e) => { setAnswer(e.target.value) }

    return (
        <div>
            <p>[הקשר: יותם עומד ללכת למיטה]<br/>
            יותם: <b>"אני ללכת לישון"</b></p>

            <label>
                <input
                    type="radio" name="answer" value="ok" checked={answer==="ok"}        
                    onChange={handleChange}
                />
                בסדר
            </label>
            <br />
            <label>
                <input
                    type="radio" name="answer" value="weird" checked={answer==="weird"}        
                    onChange={handleChange}
                />
                מוזר
            </label>
            <br/>
            <br/>
            <button onClick={() => props.func(1,answer)}>הבא</button>


        </div>
    )
}

export default Sen1